/* #region page */
html {
  font-family: 'Poppins', serif;
  font-size: 16px;
  line-height: 22.4px;
  color: #FFFFFF;
  ;
}
body {
  margin: 0;
  background-position: center;
}
/* #endregion */
/* #region header */
header {
  box-sizing: border-box;
  padding: 24px 20px 88px;
  height: 100vh;
  background-size: cover;
  background-image: url('../images/header-bg-mobile.png');
  background-position: right bottom;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header__top {
  display: flex;
  justify-content: space-between;
}
.header__icons {
  display: flex;
  gap: 32px;
}
.header__title {
    margin: 0;
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: -1.5px;

}
/* #endregion */
/* #region icon */
.icon {
display: block;
height: 24px;
width: 24px;
}

.icon--phone {
  background-image: url('/src/images/icons/Icon-Phone-call.png');
}

.icon--menu {
  background-image: url('../images/icons/Icon-Burger-menu.png');
}
/* #endregion */
